import React from "react"
import styled from "styled-components"
import GoogleMapReact from "google-map-react"
import { fitBounds } from "google-map-react/utils"
import { navigate } from "gatsby"
import { groupBy, countBy, sortBy, sample } from "lodash"

import SEO from "../components/seo"
import Header from "../components/header"
import { FlexedDiv, Text, Box } from "../components/common"
import Marker from "../components/mapMarker"

import colors from "../lib/colors"
import { getBounds, unique } from "../lib/helpers"

import searchByRouteSquare from "../images/search-by-route-square.png"
import close from "../images/close.png"

const MAX_FILTER_OPTIONS = 40

export default class Results extends React.Component {
  constructor(props) {
    super(props)

    let results = []
    let locations = []
    let description

    const { location } = this.props

    if (location.state) {
      results = location.state.results
      description = location.state.description
      locations = location.state.locations
    }

    const cities = unique(locations, "city")

    const groupedByCity = groupBy(locations, "city")

    const topCities = sortBy(cities, c => groupedByCity[c].length)
      .reverse()
      .slice(0, MAX_FILTER_OPTIONS)
      .sort()

    const tagCounts = countBy([].concat(...locations.map(l => l.tags)))

    const tagOptions = sortBy(Object.keys(tagCounts), t => tagCounts[t])
      .reverse()
      .slice(0, MAX_FILTER_OPTIONS)
      .sort()

    const defaultTag = results.length ? undefined : sample(tagOptions)

    this.state = {
      displayMap: false,
      results,
      locations,
      filterOptions: {
        state: unique(locations, "state"),
        city: unique(locations, "city"),
        topCities: topCities,
        tag: tagOptions,
      },
      filterBy: {
        state: undefined,
        city: undefined,
        tag: defaultTag,
      },
      description,
    }
  }

  componentDidMount() {
    const { filterBy, results } = this.state
    const key = Object.keys(filterBy).find(key => filterBy[key])
    if (key) {
      this.filter(key, filterBy[key])
    } else if (!results.length) {
      navigate("/")
    }
  }

  filter(key, value) {
    const { locations } = this.state
    const filterBy = {}
    filterBy[key] = value

    const results = locations
      .filter(l =>
        Object.keys(filterBy).every(key => {
          if (!filterBy[key]) return true
          return key === "tag"
            ? l.tags.indexOf(filterBy[key]) > -1
            : filterBy[key] === l[key]
        })
      )
      .map(location => ({ location }))

    let description = `${results.length} result${
      results.length === 1 ? "" : "s"
    } `

    if (filterBy["tag"]) description += "for " + filterBy["tag"]
    if (filterBy["state"]) description += "in " + filterBy["state"]
    if (filterBy["city"]) description += "in " + filterBy["city"]

    this.setState({ description, results, filterBy, selected: undefined })

    if (typeof document === undefined) return
    const scrollBox = document.getElementById("scroll-box")
    if (scrollBox) scrollBox.scroll({ top: 0 })
  }

  selected(result) {
    this.setState({ selected: result, displayMap: true })
    if (typeof document === undefined) return
    const scrollBox = document.getElementById("scroll-box")
    const element = document.getElementById(result.location.name)
    if (!element || !scrollBox) return
    scrollBox.scroll({ top: element.offsetTop, behavior: "smooth" })
  }

  render() {
    const {
      results,
      description,
      displayMap,
      filterOptions,
      filterBy,
      selected,
    } = this.state

    const bounds = getBounds(results)

    const mapContainer =
      typeof window !== "undefined" && document.getElementById("map-box")
    const width = mapContainer ? mapContainer.clientWidth : 640
    const height = mapContainer ? mapContainer.clientHeight : 380
    const size = { width, height }

    let { center, zoom } = fitBounds(bounds, size)

    if (selected) {
      center = {
        lat: selected.location.latitude,
        lng: selected.location.longitude,
      }
    }

    const displayMapBanner = results.length ? (
      <FlexedDiv
        style={{
          borderBottom: `3px solid ${colors.blue}`,
          padding: "8px 0",
          width: "100vw",
          marginLeft: "-10px",
          cursor: "pointer",
          minHeight: "35px",
        }}
        onClick={() => this.setState({ displayMap: true })}
      >
        <img
          style={{ height: "35px", width: "35px", marginRight: "5px" }}
          src={searchByRouteSquare}
          alt="search by route"
        />
        <Text small>View on Map</Text>
      </FlexedDiv>
    ) : null

    const map = results.length ? (
      <MapBox id="map-box">
        <CloseImage
          onClick={() => this.setState({ displayMap: false })}
          src={close}
        />
        <GoogleMapReact
          disableDefaultUI={true}
          bootstrapURLKeys={{ key: process.env.GATSBY_GOOGLE_API_KEY }}
          center={center}
          zoom={zoom}
        >
          {results.map(r => (
            <Marker
              key={r.location.name}
              lat={r.location.latitude}
              lng={r.location.longitude}
              name={r.location.name}
              isSelected={
                selected && selected.location.name === r.location.name
              }
              selected={() => this.selected(r)}
            />
          ))}
        </GoogleMapReact>
      </MapBox>
    ) : null

    const result = (data, idx) => {
      const isSelected =
        selected && selected.location.name === data.location.name
      return (
        <ResultBox
          highlight={isSelected}
          id={data.location.name}
          key={idx}
          onClick={() => this.selected(data)}
        >
          <InnerResultBox>
            <FlexedDiv
              style={{
                justifyContent: "space-between",
                alignItems: "flex-start",
              }}
            >
              <div style={{ textAlign: "left" }}>
                <Text
                  color={colors.blue}
                  large
                  style={{ display: "inline-block", marginRight: "10px" }}
                >
                  {data.location.name}
                </Text>

                <Text extraSmall style={{ display: "inline-block" }}>
                  {data.location.city}, {data.location.state}
                </Text>
              </div>

              {data.distance !== undefined && (
                <Text style={{ marginLeft: "20px" }} color={colors.orange}>
                  {Math.round(data.distance * 10) / 10}m
                </Text>
              )}
            </FlexedDiv>

            {data.location.tags.length && (
              <Text
                color={colors.orange}
                extraSmall
                style={{ textAlign: "left", textTransform: "capitalize" }}
              >
                {data.location.tags.sort().join(", ")}
              </Text>
            )}

            {data.location.comments && (
              <Text
                small
                color={colors.darkGray}
                style={{
                  textAlign: "left",
                  fontWeight: 400,
                  marginTop: "5px",
                  width: "calc(100% - 150px)",
                }}
              >
                {data.location.comments}
              </Text>
            )}
            {isSelected && (
              <a target="_blank" href={data.location.url}>
                <GoogleMapsLink extraSmall>Open in Google Maps</GoogleMapsLink>
              </a>
            )}
          </InnerResultBox>
        </ResultBox>
      )
    }

    return (
      <Box>
        <Header
          showFilters={true}
          filterBy={filterBy}
          filter={this.filter.bind(this)}
          filterOptions={filterOptions}
          siteTitle="Tour Food"
        />

        <SEO title="Home" />

        <ResultsBox>
          {displayMap ? map : displayMapBanner}

          {description && (
            <Text
              small
              style={{
                textAlign: "center",
                padding: "8px 0",
                width: "100vw",
                marginLeft: "-10px",
                color: "white",
                backgroundColor: colors.blue,
              }}
            >
              {description}
            </Text>
          )}

          <ScrollBox id="scroll-box">{(results || []).map(result)}</ScrollBox>
        </ResultsBox>
      </Box>
    )
  }
}

const ScrollBox = styled.div`
  width: 100%;
  margin: 0 auto;
  overflow: scroll;
  -ms-overflow-style: none;
  width: 100vw;
  margin-left: -10px;
  padding: 0 10px;
  box-sizing: border-box;
  position: relative;
`

const ResultsBox = styled.div`
  height: calc(100% - 53px);
  text-align: center;
  display: flex;
  padding: 0 10px;
  flex-direction: column;
  object-fit: contain;
`

const ResultBox = styled.div`
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: ${p => p.highlight && colors.lightestGray};
  width: 100vw;
  margin-left: -10px;
  padding: 15px 0;
`

const InnerResultBox = styled.div`
  margin: 0 auto;
  min-height: 75px;
  max-width: 750px;
  padding: 0 15px;
  position: relative;
`

const MapBox = styled.div`
  height: 50%;
  width: 100vw;
  margin-left: -10px;
  position: relative;
`

const CloseImage = styled.img`
  width: 45px;
  height: 45px;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  background-color: white;
  border-bottom: 3px solid ${colors.blue};
  border-left: 3px solid ${colors.blue};
  z-index: 500;
`

const GoogleMapsLink = styled(Text)`
  position: absolute;
  bottom: 0
  right: 0;
  text-transform: capitalize;
  border: 1px solid ${colors.orange}; 
  color: ${colors.orange};
  padding: 5px;
  margin-right: 10px;
  box-sizing: border-box;
  font-weight: 600;
`
